import TransactionModel from '../models/Transaction.model'
import { decrypt, encryptDR } from '../utils/Encryptor'
import { chainService } from '../services/Blockchain.service'

export default class BaseSmartContract {

    constructor() { }

    creatorKey = null // key owner created
    contractKey = null // ID of smart contract
    input = null

    /**
     * 
     * @param {String} creatorKey 
     * @param {String} contractKey 
     * @param {Object} inputParams 
     * @return {TransactionModel} result 
     */
    execute = (creatorKey, contractKey, inputParams) => { }

    /**
     * 
     * @param {Object} block 
     * @param {String} username 
     * @param {String} password 
     */
    verifyData = (block, username, password) => {
        const purecode = decrypt(password, block.transaction.password)
        if (purecode == 'verified' && block.transaction.username == username) {
            return true
        }

        return false
    }

    getDetailBlock = (ID) => {
        const block = chainService.chains.find(e => e.index === ID)
        return block
    }

}