import { TimeUtil } from '../utils/Time.util'
import { encrypt, decrypt } from '../utils/Encryptor'

export default class Pharmacist {

    ID = null
    name = null;
    username = null;
    password = null;

    // addition information
    pharmacyName = null
    street = null
    city = null
    zipCode = null
    address = null
    phoneNumber = null

    constructor() {
        this.ID = 'PHA_' + TimeUtil.currentUTCTimestamp().toString()
    }

    get toJson() {
        return {
            username: this.username,
            name: this.name,
            password: this.password,
            ID: this.ID
        }
    }

    get toJsonString() {
        return JSON.stringify(this.toJson)
    }

    static initFromJson = (json) => {
        let pharmacist = new Pharmacist()
        pharmacist.username = json.username
        pharmacist.name = json.name
        pharmacist.password = json.password
        pharmacist.ID = json.ID
        return pharmacist
    }

    static rootAccount = () => {
        let pharmacist = new Pharmacist()
        pharmacist.username = 'root'
        pharmacist.name = 'root'
        pharmacist.setPassword('123456x@X')
        return pharmacist
    }

    setPassword = (newValue) => {
        if (newValue)
            this.password = encrypt(newValue, 'verified')
    }

}