import BaseSmartContract from './BaseSmartcontract.smc'
import { chainService } from '../services/Blockchain.service'
import TransactionModel from '../models/Transaction.model'
import { socketService } from '../services/Socket.service'
import Prescription from '../models/Prescription.model.js'
import BlockModel from '../models/Block.model'

import PharmacyModel from '../models/Pharmacist.model'
import { pharmacyService } from '../services/Pharmacy.service'
import PharmacyRequestModel from '../models/PharmacyRequest.model'

class AddPharmacy extends BaseSmartContract {

    constructor() {
        super()
        this.creatorKey = 'FF'
        this.contractKey = 'AddPharmacySmartContract'
    }

    /**
     * 
     * @param {PharmacyModel} pharmacy 
     */
    request = (pharmacy) => {
        let requestModel = new PharmacyRequestModel()
        requestModel.pharmacyObject = pharmacy.toJson

        console.log('send data', requestModel);

        socketService.sendData({
            creatorKey: 'none',
            smcKey: this.contractKey,
            request: requestModel.json
        }, 'smc_all_miner')
    }

    execute = (creatorKey, contractKey, param) => {
        if (!param || !param.request) { return }
        const result = pharmacyService.initRequest(param.request)
        console.log(result);
    }

}

export let addPharmacySmartContract = Object.seal(new AddPharmacy())