import React, { useState, useEffect, useContext } from 'react';
import './AddPharmacy.scss';
import { useNavigate, Link, Route } from 'react-router-dom';
import { loginSmartContract } from '../../smartcontract/Login.smc'
import { authenticateService } from '../../services/Authen.service'
import { addPharmacySmartContract } from '../../smartcontract/AddPharmacy.smc'
import PharmacyModel from '../../models/Pharmacist.model'

function Login() {
    const navigate = useNavigate();
    const [username, setUsername] = useState();
    const [password, setPassword] = useState();
    const [name, setName] = useState('');
    const [error, setError] = useState(null);

    useEffect(() => {

    }, [])

    const addPharmacy = () => {
        if (username == '' || password == '' || name == '') {
            alert('Please enter at least Pharmacy name, username and password')
            return;
        }

        let pharmacy = new PharmacyModel()
        pharmacy.name = name
        pharmacy.username = username
        pharmacy.setPassword(password)
        addPharmacySmartContract.request(pharmacy)
        navigate('/bc-healthcare-pharmacy/login')
        alert('Your request sent, please wait for approval from validators.')
    }

    return (
        <div className="add-patient">
            <div className="add-patient-container">
                <h2 className="add-patient-container-title">
                    Add new agency
                </h2>
                <div className="add-patient-container-panel">
                    <div className="panel">
                        <div className="panel-left">
                            <div className="panel-left-avatar">
                                <img src="" className="img" />
                            </div>
                            <button className='main-button'>Upload image</button>

                        </div>
                        <div className="panel-right">
                            <div className='section'>Pharmacy information</div>
                            <p className='title'>Pharmacist full name</p>
                            <input type="text" placeholder='Full name' className="panel-right-input panel-right-name " />

                            <p className='title'>Pharmacy name</p>
                            <input type="text" onChange={(e) => { setName(e.target.value) }} placeholder='Name' className="panel-right-input panel-right-name " />

                            <p className='title'>Phone number</p>
                            <input type="text" placeholder='Phone number' className="panel-right-input panel-right-name " />

                            <p className='title'>Address</p>
                            <input type="text" placeholder='Address, street, city' className="panel-right-input panel-right-name " />

                            <div className='section'>Account information</div>

                            <p className='title'>Username</p>
                            <input type="text" onChange={(e) => { setUsername(e.target.value) }} placeholder='Username' className="panel-right-input panel-right-name " />

                            <p className='title'>Password</p>
                            <input type="text" onChange={(e) => { setPassword(e.target.value) }} placeholder='Password' className="panel-right-input panel-right-dob" />


                            <div className="button-bar">
                                <button className="sec-button margin-r-8" onClick={() => {
                                    navigate('/bc-healthcare-pharmacy/login')
                                }}>Back</button>
                                <button className="main-button" onClick={addPharmacy}>Create account</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

Login.propTypes = {

}

export default Login