
import { TimeUtil } from '../utils/Time.util.js'
import PharmacyModel from './Pharmacist.model'

export default class PharmacyRequestModel {

    createdTime = null
    ID = null

    pharmacyObject = null

    constructor() {
        this.createdTime = TimeUtil.currentUTCTimestamp()
        this.ID = TimeUtil.currentUTCTimestamp().toString() + 'RQ'
    }

    get json() {
        return {
            createdTime: this.createdTime,
            ID: this.ID,
            pharmacyObject: this.pharmacyObject
        }
    }

    static iniFromJson = (object) => {
        let request = new PharmacyRequestModel()
        request.ID = object.ID
        request.createdTime = object.createdTime
        request.pharmacyObject = object.pharmacyObject

        return request
    }

    /**
     * @type {PharmacyModel}
     */
    get pharmacy() {
        return PharmacyModel.initFromJson(this.pharmacyObject)
    }

}