import BaseSmartContract from './BaseSmartcontract.smc'
import { chainService } from '../services/Blockchain.service'
import TransactionModel from '../models/Transaction.model'
import { socketService } from '../services/Socket.service'
import PharmacistModel from '../models/Pharmacist.model'


class PharmacistDetail extends BaseSmartContract {

    constructor() {
        super()
        this.creatorKey = 'FF'
        this.contractKey = 'GetPharmacistDetailSmartContract'
    }

    execute = (creatorKey, contractKey, param) => {
        if (!param || !param.socketId) { return }
        if (!param || !param.username) { return }
        if (!param || !param.password) { return }

        const pharmacy = this.getDetail(param.username, param.password)
        if (pharmacy) {
            const transaction = TransactionModel.init(param.socketId, {
                smcKey: 'GetPharmacistDetailSmartContract',
                pharmacy: pharmacy.toJson
            })

            socketService.sendSMCReturn(transaction.toJson())

            return;
        }

        // else
        const transaction = TransactionModel.init(param.socketId, {
            smcKey: 'GetPharmacistDetailSmartContract',
            pharmacy: null
        })

        socketService.sendSMCReturn(transaction.toJson())
    }

    /**
     * 
     * @param {*} ID 
     * @param {*} password 
     * @returns {PharmacistModel}
     */
    getDetail = (ID, password) => {
        let block = this.getDetailBlock(ID)

        if (block) {

            if (block.direction) {
                block = chainService.getShadowOf(block.direction)
            }

            const confirmed = this.verifyData(block, ID, password)
            if (confirmed == true) {
                const object = PharmacistModel.initFromJson(block.transaction)
                return object
            }
        }

        console.log('Can not get pharmacy information, wrong password or userId');
        return undefined
    }

    /**
     * 
     * @param {*} ID 
     * @param {*} password 
     * @returns {BlockModel}
     */
     getDetailBlock = (ID) => {
        const result = chainService.chains.filter(e => {
            return (e.type === 'pharmacy')
        }).find(e => {
            const object = PharmacistModel.initFromJson(e.transaction)
            if (object.username == ID) {
                return true
            }

            return false
        })

        return result
    }

}

export let getPharmacistDetailSmartContract = Object.seal(new PharmacistDetail())