import React, { useState, useEffect, useContext } from 'react';
import './Login.scss';
import { useNavigate, Link, Route } from 'react-router-dom';
import { loginSmartContract } from '../../smartcontract/Login.smc'
import { authenticateService } from '../../services/Authen.service'

function Login() {
    const navigate = useNavigate();
    const [username, setUsername] = useState();
    const [password, setPassword] = useState();
    const [error, setError] = useState(null);

    useEffect(() => {

    }, [])

    const toHome = () => {
        navigate('/bc-healthcare-pharmacy/home')
    }

    const login = () => {
        const object = loginSmartContract.internalLogin(username, password)
        if (object) {
            authenticateService.authenWith(username)
            toHome()
        }
        else {
            setError(true)
        }
    }

    const newAccount = () => {
        navigate('/bc-healthcare-pharmacy/AddPharmacy')
    }

    return (
        <div className="body-all">
            <div className="content-view">
                <div className="title-text font-gg">Welcome to Pharmacy Website</div>
                <div className="desc-text font-gg">Login with your provided account to access BC system.</div>
                {
                    error ? <div className="err-text font-gg">Wrong user name or password</div> : null
                }
                <input
                    type="text"
                    onChange={(e) => { setUsername(e.target.value) }}
                    placeholder='User name'
                    className="text-input font-gg"
                    defaultValue={''}
                />
                <input
                    type="password"
                    onChange={(e) => { setPassword(e.target.value) }}
                    placeholder='Password'
                    className="text-input font-gg"
                    defaultValue={''}
                />
                <button className='main-button font-gg' onClick={login} style={{ marginLeft: 8 }}><div>Login</div></button>
                <div className='font-gg'>or</div>
                <button className='button font-gg new-account' onClick={newAccount} style={{ marginLeft: 8 }}><div>Create new account</div></button>
            </div>
        </div>
    )
}

Login.propTypes = {

}

export default Login