import PharmacyRequest from '../models/PharmacyRequest.model'

class PharmacyService {

    /**
     * @type {PharmacyRequest[]} request
     */
    requests = []

    initRequest = (requestObject) => {
        let request = PharmacyRequest.iniFromJson(requestObject)
        if (this.validate(request)) {
            this.requests.push(request)
            this.onReceiveRequest()
            return true
        }

        return false
    }

    /**
     * 
     * @param {PharmacyRequest} pharmacyRQ 
     */
    validate = (pharmacyRQ) => {
        if (this.requests.find(e => e.ID == pharmacyRQ.ID)) {
            return false
        }

        if (this.requests.find(e => {
            return (e.pharmacy.ID == pharmacyRQ.pharmacy.ID) || (e.pharmacy.username == pharmacyRQ.pharmacy.username)
        })) {
            return false
        }

        return true
    }

    reject = (ID) => {
        let index = this.requests.findIndex(e => e.ID == ID)
        if (index >= 0) {
            this.requests.splice(index, 1);
        }
    }

    onReceiveRequest = () => {
        
    }

}



export let pharmacyService = Object.seal(new PharmacyService())