import React, { useState, useEffect, useContext } from 'react';
import './RequestList.scss';
import { useNavigate, Link, Route } from 'react-router-dom';
import PharmacyRequestModel from '../../models/PharmacyRequest.model'
import { pharmacyService } from '../../services/Pharmacy.service'

export default class RequestList extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            requests: pharmacyService.requests
        }
    }

    closePopup = () => {
        if (this.props.onClose) {
            this.props.onClose()
        }
    }

    approve = () => {

    }

    reject = (ID) => {
        console.log(ID);
        pharmacyService.reject(ID)
        //pharmacyService.requests.splice(0,1)
        if (pharmacyService.requests.length == 0) {
            this.props.onClose()
        }
        else {
            this.setState({ requests: pharmacyService.requests })
        }
    }

    render() {
        return (
            <div className='background'>
                <div className='background-content-view'>
                    <div className='close-view'>
                        <p className='close-view-title font-gg'>New account requests list</p>
                        <button className='logout-button font-gg margin-l-8' onClick={this.closePopup}>Close</button>
                    </div>
                    <div className='list-request-view'>
                        {this.state.requests.map(e => {
                            console.log(e);
                            return (
                                <div key={e.ID} className="request-view">
                                    <div className='request-content font-gg'>Request ID: {e.ID}</div>
                                    <div className='request-content font-gg'>Pharmacy ID: {e.pharmacy.ID}</div>
                                    <div className='request-content font-gg'>Pharmacy name: {e.pharmacy.name}</div>
                                    <div className='request-content font-gg'>Username: {e.pharmacy.username}</div>
                                    <div className='request-content font-gg'>Address: {e.pharmacy.address}</div>
                                    <div className='margin-top-4'>
                                        <button className='main-button font-gg' onClick={this.approve}>Approve</button>
                                        <button className='sec-button font-gg margin-l-8' onClick={() => { this.reject(e.ID) }}>Reject</button>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            </div>
        )
    }
}