import React from 'react'
import { authenticateService } from '../services/Authen.service'
import { Navigate, Route } from 'react-router-dom'

const AuthenRoute = ({ children }) => {

  // Add your own authentication on the below line.
  const isLoggedIn = authenticateService.isAuthenticated()
  return isLoggedIn ? children : <Navigate to="/bc-healthcare-pharmacy/login" />

}

export default AuthenRoute