import React from 'react'
import Home from "../pages/Home"
import AddPatient from "../pages/AddPatient"
import AddDoctor from "../pages/AddDoctor"
import { Route, Routes } from 'react-router-dom'
import StoreProvider from '../context/StoreContext'
import Login from "../pages/Login"
import AddPharmacy from "../pages/AddPharmacy"
import AuthenRoute from '../components/AuthenRoute'

//bc-healthcare-pharmacy

function Rou() {
    return (
        <StoreProvider>
            <Routes>
                <Route exact path="/Login" element={<Login />} />
                <Route exact path="/bc-healthcare-pharmacy/Login" element={<Login />} />
                <Route exact path="/" element={<AuthenRoute><Home /></AuthenRoute>} />
                <Route exact path="/bc-healthcare-pharmacy" element={<AuthenRoute><Home /></AuthenRoute>} />
                <Route exact path="/home" element={<AuthenRoute><Home /></AuthenRoute>} />
                <Route exact path="/add-patient" element={<AuthenRoute><AddPatient /></AuthenRoute>} />
                <Route exact path="/edit-patient" element={<AuthenRoute><AddPatient /></AuthenRoute>} />
                <Route exact path="/add-doctor" element={<AuthenRoute><AddDoctor /></AuthenRoute>} />
                <Route exact path="/bc-healthcare-pharmacy/home" element={<AuthenRoute><Home /></AuthenRoute>} />
                <Route exact path="/bc-healthcare-pharmacy/add-patient" element={<AuthenRoute><AddPatient /></AuthenRoute>} />
                <Route exact path="/bc-healthcare-pharmacy/edit-patient" element={<AuthenRoute><AddPatient /></AuthenRoute>} />
                <Route exact path="/bc-healthcare-pharmacy/add-doctor" element={<AuthenRoute><AddDoctor /></AuthenRoute>} />
                <Route exact path="/AddPharmacy" element={<AddPharmacy />} />
                <Route exact path="/bc-healthcare-pharmacy/AddPharmacy" element={<AddPharmacy />} />
            </Routes>
        </StoreProvider>
    )
}
export default Rou